/* style for profile image*/
.profile-image {
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

/* style for profile image and profile info summary */
.flex-container {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  justify-content: center;
}

.profile-picture {
  margin-right: 8%;
}

.profile-picture-summary {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

#profile-username {
  margin-bottom: 10px;
  font-weight: bold;
}

.profile-fullname {
  font-weight: 500;
}

.spacing {
  margin-left: 80px;
}

/* remove outline from modal */
.Profile-paper-2 {
  outline: 0;
  border-radius: 5px;
  border: 2px solid rgb(215, 215, 215);
}

/* style for edit icon modal heading */
.heading-h2 {
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 10px;
}

.dispNone {
  display: none;
}

/* style for grid-list-container */
.images-container {
  margin-top: 1.5em;
  margin-bottom: 2.5em;
}

/* image modal styling */
.image-modal-left {
  width: 50%;
  height: 100%;
}

.clicked-image {
  height: 90%;
  width: 95%;
}

.image-modal-right {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.image-modal-profile-icon {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  margin-bottom: 5px;
}

.image-modal-username {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 10px;
  font-weight: bold;
  padding-bottom: 10px;
}

/* remove outline from image modal */
.Profile-modalStyle-5 {
  outline: 0;
  border-radius: 5px;
  border: 2px solid rgb(215, 215, 215);
  overflow: overlay;
}

.right-top {
  margin-bottom: 5px;
}

.right-top-image-username-container {
  display: flex;
}

/* style for to space between right middle and bottom container */
.right-middleBottom-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.comments-container {
  margin-top: 10px;
}
