.app-header {
  display: flex;
  height: 50px;
  background-color: #263238;
  padding: 10px;
  align-items: center;
  justify-content: space-between;
}

#logo {
  color: #fff;
  font-size: 18px;
  text-decoration-line: none;
}

/* Styling for Search box */
.header-searchbox {
  margin-left: auto;
  width: 300px;
  display: flex;
  align-items: center;
  background-color: #c0c0c0;
  border-radius: 4px;
}

#search-icon {
  padding-left: 5px;
}

/* styling for the profile icon  */
#profile-icon {
  margin-left: 30px;
  height: 50px;
  width: 50px;
  padding: 0;
  border: 2px solid white;
}

/* styling for the profile picture */
#profile-picture {
  height: 46px;
  width: 46px;
  border-radius: 50%;
}

/* styling for the menu when clicked on profile picture */
#profile-menu {
  margin-top: 50px;
}

/* styling for the horizontal line separator  */
.horizontal-line {
  border-bottom: groove;
  width: 80%;
  margin-left: 10%;
}

/* style for the menu list */
.MuiMenu-list {
  background: rgb(223, 223, 223);
}

.MuiList-padding {
  outline: none;
}
